import logo from './logo.svg';
import './App.css';
import React from 'react'
import L from 'leaflet'
import {MapContainer, TileLayer, Marker, Popup} from 'react-leaflet'
import 'leaflet/dist/leaflet.css';
import Card from 'react-bootstrap/Card'
import Button from 'react-bootstrap/Button'
import 'bootstrap/dist/css/bootstrap.min.css';
import icon from 'leaflet/dist/images/marker-icon.png';
import iconShadow from 'leaflet/dist/images/marker-shadow.png';
import icm from './teste1.jpg';
import locations from './locations.json';

for (var i=0; i < locations.length; i++){
  var obj = locations[i];
  console.log(`Name: ${obj.Nome}`);
}

let DefaultIcon = L.icon({
  iconUrl: icon,
  shadowUrl: iconShadow,
  iconAnchor: [12, 41]
});

L.Marker.prototype.options.icon = DefaultIcon;

const location = [-8.334, -36.42044];
const zoom = 7;


function App() {

  return (
    <div className="App">
      <MapContainer center={location} zoom={zoom}>
      <TileLayer url="https://tiles.stadiamaps.com/tiles/osm_bright/{z}/{x}/{y}{r}.png" attribution='&copy; <a href="https://stadiamaps.com/">Stadia Maps</a>, &copy; <a href="https://openmaptiles.org/">OpenMapTiles</a> &copy; <a href="http://openstreetmap.org">OpenStreetMap</a> contributors' />
      {locations.map((val, idx) =>
        <Marker key={idx} position={[val.Latitude, val.Longitude]}>
          <Popup>
            <Card style={{ width: '18rem' }}>
            <Card.Img variant="top" src={icm} />
            <Card.Body>
              <Card.Title>{val.Nome}</Card.Title>
              <Card.Text>
                {val.Endereco}
              </Card.Text>
              <Button variant="primary">Iniciar navegao</Button>
            </Card.Body>
          </Card>

          </Popup>
        </Marker>
      )}
      </MapContainer>
    </div>
  );
}

export default App;
